import React from 'react';
import { X, Loader2, CheckCircle, Clock, AlertCircle, ClockAlert, XCircle } from 'lucide-react';
import { useCheckInAttendance } from '../../hooks/useClasses';

interface CheckInModalProps {
  isOpen: boolean;
  meetingId: number | null;
  courseName: string;
  onClose: () => void;
}

const CheckInModal: React.FC<CheckInModalProps> = ({
  isOpen,
  meetingId,
  courseName,
  onClose,
}) => {
  const [selectedStatus, setSelectedStatus] = React.useState<'Hadir' | 'Izin' | 'Sakit' | 'Alfa' | 'Terlambat' | 'Pengganti'>('Hadir');
  const [description, setDescription] = React.useState('');
  const [linkBukti, setLinkBukti] = React.useState('');

  const checkInMutation = useCheckInAttendance();

  // Reset form when modal opens
  React.useEffect(() => {
    if (isOpen) {
      setSelectedStatus('Hadir');
      setDescription('');
      setLinkBukti('');
    }
  }, [isOpen]);

  const handleCheckIn = async () => {
    if (!meetingId || checkInMutation.isPending) return;

    try {
      await checkInMutation.mutateAsync({
        id_pertemuan: meetingId,
        status_kehadiran: selectedStatus,
        deskripsi: description.trim() || undefined,
        link_bukti: linkBukti.trim() || undefined
      });

      onClose();

      const statusLabels = {
        'Hadir': 'Present',
        'Izin': 'Permission',
        'Sakit': 'Sick',
        'Alfa': 'Absent',
        'Terlambat': 'Late',
        'Pengganti': 'Substitute',
      };
      alert(`Successfully checked in as ${statusLabels[selectedStatus]}!`);
    } catch (err) {
      console.error('Check-in failed:', err);
      alert(`Check-in failed: ${err instanceof Error ? err.message : 'Unknown error'}`);
    }
  };

  if (!isOpen) return null;

  return (
    <>
      <div className="fixed inset-0 bg-black/50 z-40" onClick={onClose} />
      {/* Modal */}
      <div className="fixed inset-0 flex items-center justify-center z-50 p-4">
        <div className="bg-white rounded-lg shadow-xl max-w-md w-full max-h-[90vh] overflow-y-auto">
          <div className="flex items-center justify-between p-4 border-b border-gray-200 sticky top-0 bg-white">
            <h3 className="text-base lg:text-lg font-semibold text-gray-900">Check-in Attendance</h3>
            <button
              onClick={onClose}
              className="text-gray-400 hover:text-gray-600 transition-colors cursor-pointer"
              disabled={checkInMutation.isPending}
            >
              <X className="h-5 w-5" />
            </button>
          </div>

          <div className="p-4 space-y-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">Course</label>
              <div className="text-sm text-gray-900">{courseName}</div>
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">Attendance Status *</label>
              <div className="grid grid-cols-2 gap-2">
                {(['Hadir', 'Izin', 'Sakit', 'Alfa', 'Terlambat', 'Pengganti'] as const).map(status => {
                  const getStatusIcon = () => {
                    switch (status) {
                      case 'Hadir':
                        return <CheckCircle className="h-4 w-4" />;
                      case 'Izin':
                        return <Clock className="h-4 w-4" />;
                      case 'Sakit':
                        return <AlertCircle className="h-4 w-4" />;
                      case 'Terlambat':
                        return <ClockAlert className="h-4 w-4" />;
                      case 'Pengganti':
                        return <CheckCircle className="h-4 w-4" />;
                      case 'Alfa':
                        return <XCircle className="h-4 w-4" />;
                    }
                  };

                  return (
                    <button
                      key={status}
                      onClick={() => setSelectedStatus(status)}
                      disabled={checkInMutation.isPending}
                      className={`px-3 lg:px-4 py-2 text-xs lg:text-sm font-medium rounded-lg transition-colors cursor-pointer flex items-center justify-center gap-1.5 ${
                        selectedStatus === status
                          ? status === 'Hadir'
                            ? 'bg-green-600 text-white'
                            : status === 'Alfa'
                            ? 'bg-red-600 text-white'
                            : status === 'Pengganti'
                            ? 'bg-purple-600 text-white'
                            : status === 'Terlambat'
                            ? 'bg-orange-600 text-white'
                            : status === 'Izin'
                            ? 'bg-blue-600 text-white'
                            : 'bg-yellow-600 text-white'
                          : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                      } ${checkInMutation.isPending ? 'opacity-50 cursor-not-allowed' : ''}`}
                    >
                      {getStatusIcon()}
                      <span className="hidden sm:inline">
                        {status === 'Hadir' && 'Present'}
                        {status === 'Izin' && 'Permission'}
                        {status === 'Sakit' && 'Sick'}
                        {status === 'Alfa' && 'Absent'}
                        {status === 'Terlambat' && 'Late'}
                        {status === 'Pengganti' && 'Substitute'}
                      </span>
                      <span className="sm:inline md:hidden">
                        {status}
                      </span>
                    </button>
                  );
                })}
              </div>
            </div>

            <div>
              <label htmlFor="link-bukti" className="block text-sm font-medium text-gray-700 mb-1">
                Proof Link (Optional)
              </label>
              <input
                id="link-bukti"
                type="url"
                value={linkBukti}
                onChange={(e) => setLinkBukti(e.target.value)}
                disabled={checkInMutation.isPending}
                placeholder="https://example.com/proof.jpg"
                className="input-field w-full text-sm"
                maxLength={500}
              />
              <div className="text-xs text-gray-500 mt-1">
                Link to attendance proof (e.g., photo, document)
              </div>
            </div>

            <div>
              <label htmlFor="description" className="block text-sm font-medium text-gray-700 mb-1">
                Description (Optional)
              </label>
              <textarea
                id="description"
                value={description}
                onChange={(e) => setDescription(e.target.value)}
                disabled={checkInMutation.isPending}
                placeholder="Add a note about your attendance..."
                rows={3}
                className="input-field w-full text-sm resize-none"
                maxLength={500}
              />
              <div className="text-xs text-gray-500 mt-1">
                {description.length}/500 characters
              </div>
            </div>
          </div>

          <div className="flex flex-col sm:flex-row sm:items-center sm:justify-end gap-2 sm:gap-3 p-4 border-t border-gray-200 sticky bottom-0 bg-white">
            <button
              onClick={onClose}
              disabled={checkInMutation.isPending}
              className="w-full sm:w-auto px-4 py-2 text-sm font-medium text-gray-700 bg-gray-100 rounded-lg hover:bg-gray-200 transition-colors disabled:opacity-50 disabled:cursor-not-allowed cursor-pointer order-2 sm:order-1"
            >
              Cancel
            </button>
            <button
              onClick={handleCheckIn}
              disabled={checkInMutation.isPending}
              className="w-full sm:w-auto px-4 py-2 text-sm font-medium text-white bg-primary-600 rounded-lg hover:bg-primary-700 transition-colors disabled:opacity-50 disabled:cursor-not-allowed cursor-pointer flex items-center justify-center gap-2 order-1 sm:order-2"
            >
              {checkInMutation.isPending ? (
                <>
                  <Loader2 className="h-4 w-4 animate-spin" />
                  <span>Checking in...</span>
                </>
              ) : (
                <span>Confirm Check-in</span>
              )}
            </button>
          </div>
        </div>
      </div>
    </>
  );
};

export default CheckInModal;
